import {useEffect, useState} from "react";
import {Container, Nav, Navbar} from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faGithub, faLinkedin, faTwitter} from "@fortawesome/free-brands-svg-icons";
import logo from '../assets/Blue (3).png';

export const NavBar =()=>{
    const [activeLink, setActiveLink] = useState('home');
    const [scrolled,seScrolled] = useState(false);

    useEffect(() =>{
        const onScroll = () => {
            if(window.scrollY>50){
                seScrolled(true);
            }else {
                seScrolled(false);
            }
        }

        window.addEventListener("scroll",onScroll);
        return () => window.removeEventListener("scroll",onScroll);
    },[])

    const onUpdateActiveLink = (value) =>{
        setActiveLink(value);
    }
        return (
            <Navbar expand="lg" className={scrolled ? "scrolled":""}>
                <Container>
                    <Navbar.Brand href="#home">
                        <img src={logo} alt="Logo"/>
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav">
                        <span className="navbar-toggler-icon"></span>
                    </Navbar.Toggle>
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="me-auto">
                            <Nav.Link href="#home" className={activeLink==='home' ? 'active navbar-link': 'navbar-link'} onClick={()=> onUpdateActiveLink('home')}>Home</Nav.Link>
                            <Nav.Link href="#skills"  className={activeLink==='skills' ? 'active navbar-link': 'navbar-link'} onClick={()=> onUpdateActiveLink('skills')}>Skills</Nav.Link>
                            <Nav.Link href="#projects"  className={activeLink==='projects' ? 'active navbar-link': 'navbar-link'} onClick={()=> onUpdateActiveLink('projects')}>Projects</Nav.Link>
                        </Nav>
                        <span className="navbar-text">
                            <div className="social-icon">
                                <a href="https://github.com/kingslysam"
                                   target="_blank"
                                   rel="noreferrer">
                                    <FontAwesomeIcon icon={faGithub} color="#fff"/>
                                </a>
                                <a href="https://www.linkedin.com/in/samuel-killagane-08471923b/"
                                   target="_blank"
                                   rel="noreferrer">
                                    <FontAwesomeIcon icon={faLinkedin} color="#fff"/>
                                </a>
                                <a href="https://twitter.com/samuelkingslyy"
                                   target="_blank"
                                   rel="noreferrer">
                                    <FontAwesomeIcon icon={faTwitter} color="#fff"/>
                                </a>
                            </div>
                        <button className="vvd" onClick={() => console.log('connect')}>
                            <span>
                                <Nav.Link href="#connect" color="#fffff">Let's connect</Nav.Link>
                            </span>
                        </button>
                        </span>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
    )
}